import React from "react";
import { Modal } from "react-bootstrap";


const BootstrapModal = ({
  show,
  onClose,
  children,
  title,
  onSubmitClick,
  size,
  id,
  onCancel,
  buttonLabels,
}: {
  id?: string;
  show: any;
  onClose: any;
  children: any;
  title: string;
  onSubmitClick: any;
  size?: any;
  onCancel?: any;
  buttonLabels?: { submit: string; cancel: string };
}) => {
  return (
    <div className="container">
      <Modal show={show} onHide={onClose} size={size} id={`${id}-modal`} centered={true} style={{marginTop:"3rem"}}>
        <Modal.Header closeButton>
          <Modal.Title style={{ fontWeight: "bold" }}>{title}</Modal.Title>
        </Modal.Header>
        <Modal.Body id={`${id}-modal-body`} style={{maxHeight:"400px"}}>{children}</Modal.Body>
        <Modal.Footer id={`${id}-modal-footer`}>
          {onSubmitClick && (
            <button className="btn btn-primary" onClick={onSubmitClick}>
              {buttonLabels?.submit || "Submit"}
            </button>
          )}

         {onCancel && <button className="btn btn-primary" onClick={onCancel || onClose}>
            {buttonLabels?.cancel || "Cancel"}
          </button>} 
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default BootstrapModal;
